export var isFunction = function isFunction(type) {
  return typeof type === "function";
};
export var isString = function isString(type) {
  return typeof type === "string";
};
export var isNumber = function isNumber(type) {
  return typeof type === "number";
};
export var isArray = function isArray(type) {
  return Array.isArray(type);
};
export var isObject = function isObject(type) {
  return type === Object(type) && !isArray(type);
};
export var isNullOrUndefined = function isNullOrUndefined(type) {
  return type !== null && typeof type !== "undefined";
};